*, :before, :after {
  box-sizing: border-box;
}

body {
  color: #2a2a2a;
  font-family: Open Sans, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, sans-serif;
  line-height: 1.5;
}

.search-form {
  background-color: #5d5db5;
  border-radius: 3px;
  justify-content: center;
  margin-bottom: 24px;
  padding: 8px;
  display: flex;
}

.button {
  margin-left: 10px;
  display: block;
}

.gallery {
  grid-gap: 20px;
  max-width: calc(100vw - 80px);
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  margin: 0 auto 30px;
  display: grid;
}

.photo-card {
  box-shadow: 0 1px 1px #828080;
}

.wrapper {
  height: 200px;
  overflow: hidden;
}

.info {
  justify-content: space-around;
  display: flex;
}

.info-item {
  text-align: center;
  flex-direction: column;
  margin-top: 10px;
  display: flex;
}

img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  transition: transform .5s ease-out;
  display: block;
  transform: scale(1);
}

img:hover {
  transform: scale(1.15);
}

